:root{--border-radius: 10px;--border-radius-btn: 5px;--box-shadow-color: 0, 0, 0;--box-shadow: 0px 5px 12px 0px rgba(var(--box-shadow-color), 0.2);--background-color-main: #282c34;--darker-white: #f6f9fc;--text-grey: #20303c;--regular-white: #ffffff;--mint-green: #42b883;--link-blue: #1e90ff;--danger-red: #dc3545;--search-results-border: #ced4da;--font-size: 16px;--line-height: 20px;--font-size-sm: 12px;--line-height-sm: 14px;--font-size-xs: 10px;--line-height-xs: 12px;--text-font-family: 'Quicksand'}.app{display:grid;background-color:#282c34;background-color:var(--background-color-main);min-height:100vh;height:100vh;width:100vw;grid-template-rows:50px 60px 1fr;grid-template-columns:minmax(400px, 30%) auto;grid-template-areas:'header header' 'buttons-section location-details-section' 'locations-list-section location-details-section' 'locations-list-section location-details-section'}@media screen and (max-width: 800px){.app{height:100%;grid-template-rows:50px 60px 300px auto;grid-template-columns:auto;grid-template-areas:'header ' 'buttons-section' 'locations-list-section' 'location-details-section'}}

.app-header{grid-area:header;display:flex;background-color:var(--background-color-main);color:var(--darker-white);justify-content:space-between;align-items:center;padding-left:10px;padding-right:10px}.app-header a{text-decoration:none}.app-header a:link,.app-header a:visited,.app-header a:hover,.app-header a:active{color:var(--darker-white)}

.location-info-container{position:absolute;z-index:100;bottom:50px;left:20px;max-width:600px;width:40%;background-color:#fff;padding:10px;box-shadow:var(--box-shadow);border-radius:var(--border-radius)}@media screen and (max-width: 800px){.location-info-container{position:relative;width:100%;bottom:unset;left:unset;border-radius:unset;font-size:var(--font-size-sm);line-height:var(--line-height-sm)}}

.map-container{width:100%;height:100%}.map-container img{width:100%;height:100%}

.location-details-container{grid-area:location-details-section;position:relative;background-color:var(--darker-white);width:100%;height:100%;border-left:1px solid var(--background-color-main)}@media screen and (max-width: 800px){.location-details-container{border:none;border-top:10px solid var(--darker-white)}}.location-details-placeholder{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);width:400px;height:200px;display:flex;flex-direction:column;align-items:center;text-align:center}@media screen and (max-width: 800px){.location-details-placeholder{width:100%;height:100%;font-size:var(--font-size-sm);transform:translate(-50%, -25%)}}

.location-list-container{grid-area:locations-list-section;background-color:var(--darker-white);padding:10px;display:flex;flex-direction:column;align-items:center;overflow-y:auto}

.location-item-container{cursor:pointer;position:relative;background-color:var(--regular-white);color:var(--text-grey);padding:20px 10px;box-shadow:var(--box-shadow);margin-bottom:10px;width:100%;border-radius:5px}.location-item-container.selected-location{border:2px solid var(--mint-green)}@media screen and (max-width: 800px){.location-item-container{padding:10px 5px;font-size:var(--font-size-sm);line-height:var(--line-height-sm)}}.remove-location-btn{cursor:pointer;position:absolute;top:5px;right:5px;font-size:20px;background-color:var(--regular-white);border:none;border-radius:var(--border-radius);width:20px;height:20px}.location-item-address{font-size:var(--font-size-sm)}@media screen and (max-width: 800px){.location-item-address{font-size:var(--font-size-xs);line-height:var(--line-height-xs)}}

.location-search-container{grid-area:locations-list-section;background-color:var(--darker-white);padding:10px;display:flex;flex-direction:column}.location-search-title{margin-bottom:15px;padding-left:10px}@media screen and (max-width: 800px){.location-search-title{margin-bottom:5px;padding:0 5px;font-size:var(--font-size-sm);line-height:var(--line-height-sm)}}.location-search-input{width:100%;border:none;box-shadow:var(--box-shadow);padding:10px;font-size:var(--font-size);font-family:inherit;outline-color:var(--mint-green);margin-bottom:10px}.location-search-input:focus{outline-style:solid}.location-search-information{padding:10px;outline-color:var(--mint-green);box-shadow:var(--box-shadow);border:none}.location-search-information:focus{outline-style:solid}.location-search-results{width:100%;box-shadow:var(--box-shadow)}.location-search-results-list{list-style-type:none;background-color:var(--regular-white);padding:0;margin:0}.location-search-results-item{cursor:pointer;background-color:var(--regular-white);text-align:center;padding-top:10px;padding-bottom:10px}.location-search-results-item:first-child{margin-top:5px}.location-search-results-item:not(:last-child){border-bottom:1px solid var(--search-results-border)}.location-search-results-item:hover{background-color:var(--darker-white)}@media screen and (max-width: 800px){.location-search-results-item{padding:7px 5px;font-size:var(--font-size-sm);line-height:var(--line-height-sm)}}

.buttons-container{grid-area:buttons-section;background-color:var(--darker-white);display:flex;align-items:center;padding:20px 0 10px 10px}@media screen and (max-width: 800px){.buttons-container{padding:10px}}.menu-btn{cursor:pointer;width:140px;height:40px;border-radius:var(--border-radius-btn);font-family:var(--text-font-family);border:none;font-size:var(--font-size)}@media screen and (max-width: 800px){.menu-btn{width:100%}}.add-location-btn{background-color:var(--link-blue);color:var(--regular-white)}.close-btn{background-color:var(--danger-red);color:var(--regular-white)}

